<template>
  <div class="part-three" id="part-three"></div>
</template>

<script>

  // 
  let echarts = require("echarts/lib/echarts");

  export default {
    name: "partThree",
    data() {
      return {};
    },
    mounted() {
      this.drawECharts();
    },
    methods: {
      drawECharts() {
        // domecharts
        let myChart = echarts.init(document.getElementById("part-three"));

        myChart.setOption({
          // 
          title: {
            // 
            text: "",
            // 
            textStyle: {
              color: "#fff"
            },
            // 
            x: "center"
          },
          // 
          tooltip: {
            trigger: "item",
            // 
            formatter: "{a} <br/>{b} : {d}%"
          },
          // 
          legend: {
            x: "center",
            y: "bottom",
            textStyle: {
              color: "#fff"
            },
            data: [ "HTML5/CSS3", "JavaScript", "Node", "Vue", "Angular", "Gulp", "" ]
          },
          // 
          calculable: true,
          // 
          series: [
            {
              name: "",
              type: "pie",
              radius: [30, 110],
              // radius area
              roseType: "radius", //radius  area
              data: [
                { value: 20, name: "HTML5/CSS3" },
                { value: 19, name: "JavaScript" },
                { value: 15, name: "Node" },
                { value: 18, name: "Vue" },
                { value: 12, name: "Angular" },
                { value: 8, name: "Gulp" },
                { value: 5, name: "" }
              ]
            }
          ],
          // 
          color: ['#99CCCC','#CCFFCC','#FFFFCC','#CCFFFF','#99CCFF',"#66CCCC","#CCFF99"]
        });
      }
    }
  };
</script>

<style scoped>
  .part-three {
    width: 100%;
    height: 500px;
    background: #94e7ff;
    box-shadow: #99CCCC 0px 0px 5px ;
  }
</style>



